                    <div style="padding: 20px 0px;height:200px">
                      <el-input type="textarea" v-model="bankProd.jssm" :rows="7" placeholder="请输入结算说明"></el-input>
                    </div>
                  </el-tab-pane>
                  <el-tab-pane label="申请流程说明" name="first7">
                    <div style="padding: 20px 0px;height:200px">
                      <el-input type="textarea" v-model="bankProd.lcsm" :rows="7" placeholder="请输入申请流程说明"></el-input>
                    </div>
                  </el-tab-pane>
