                <el-form-item label="录入日期" :label-width="formLabelWidth2">
                  <el-input v-model="lrrq" auto-complete="off" disabled="disabled"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="修改人员" :label-width="formLabelWidth2">
                  <el-input v-model="xgry" auto-complete="off" disabled="disabled"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="修改日期" :label-width="formLabelWidth2">
                  <el-input v-model="xgrq" auto-complete="off" disabled="disabled"></el-input>
                </el-form-item>
              </el-col>
