</template>
<style lang="scss" scoped>
@import './productManage.scss';
</style>
<script>
import Axios from 'axios';
var menu_uuid = 'EE49EC3920DA49B7876A2CFA479458B8';
export default { 
  data() {
        return {
          loading:false,
            isShowDialog:false,
            dialogTableLrxx:false,
            isShowImg:false,
            activeName:'first',
            search_cpmc:'',
            tableList:[],
            selectList:[],
            cplxList:[],
            isFileIcon:'',
            formLabelWidth2:'100px',
            bankProd:{
                prodUuid:'',
                prodName:'',//产品名称
                prodZt:'',//产品状态（Y或N）
                prodSm:'',//产品说明
                prodTj:'',//产品条件
                prodTd:'',//产品特点
                prodSxzl:'',//所需资料
                prodFl:'',//产品分类（01企业、02个人）
                maxJe:'',//最大放款金额
                jjed:'',//	件均额度
                cgfks:'',//成功放款数
                zkfksj:'',//最快放款时间
                dkqs:'',//贷款期数
                dkll:'',//贷款利率
                hkfs:'',//还款方式
                dq:'',//地区
                byfy:'',//初级返拥
                hjfy:'',//中级返拥
                zsfy:'',//高级返拥
                jszq:'',//结算周期
                jstj:'',//结算条件
                jssm:'',//结算说明
                lcPdf:'',//流程PDF
                bankUrl:'',//	跳转银行链接
                tghbUrl:'',//	推广海报URL
                xh:'',//排序序号
                logoUrl:'',//LOGO地址
                ygmyhk:'',//预估每月还款
                tkmUrl:'',//提款码地址
                imgSrc:'',
                lcsm:'',//申请流程说明
                kfUrl:'',//客服url
                kfBz:''
            },
            lrrq:'',
            lrry:'',
            xgry:'',
            xgrq:'',
            currentPage:0,
            totalSize:0,
            classifyList:[],
            proeTtile:'新增产品',
        }
  },
  methods: {
      searchFun(){
        this.querTableData(this.search_cpmc, 1);
      },
      addProd(){
        this.bankProd={
                prodUuid:'',
                prodName:'',//产品名称
                prodZt:'',//产品状态（Y或N）
                prodSm:'',//产品说明
                prodTj:'',//产品条件
                prodTd:'',//产品特点
                prodSxzl:'',//所需资料
                prodFl:'',//产品分类（01企业、02个人）
                maxJe:'',//最大放款金额
                jjed:'',//	件均额度
                cgfks:'',//成功放款数
                zkfksj:'',//最快放款时间
                dkqs:'',//贷款期数
                dkll:'',//贷款利率
                hkfs:'',//还款方式
                dq:'',//地区
                byfy:'',//初级返拥
                hjfy:'',//中级返拥
                zsfy:'',//高级返拥
                jszq:'',//结算周期
                jstj:'',//结算条件
                jssm:'',//结算说明
                lcPdf:'',//流程PDF
                bankUrl:'',//	跳转银行链接
                tghbUrl:'',//	推广海报URL
                xh:'',//排序序号
                logoUrl:'',//LOGO地址
                ygmyhk:'',//预估每月还款
                tkmUrl:'',//提款码地址
                imgSrc:'',
                lcsm:'',//申请流程说明
                kfUrl:'',
                kfBz:''
            },
        this.lrrq='';
        this.lrry='';
        this.xgry='';
        this.xgry='';
        this.isShowImg=false;
        this.dialogTableLrxx = false;
        this.isShowDialog=true;
        this.proeTtile="新增产品";
      },
      editProd(){
        this.activeName = 'first';
        this.dialogTableLrxx = true;
        if (this.selectList == '' || this.selectList == null) {
          this.$message({
            type: 'info',
            message: '请点击一条数据操作!'
          });
          return false;
        }
        if (this.selectList.prodZt == 'N') {
          this.$message({
            type: 'info',
            message: '无效数据不能编辑!'
          });
          return false;
        }
        this.proeTtile="编辑产品";
        this.bankProd={
            prodUuid:this.selectList.prodUuid,
            prodName:this.selectList.prodName,
            prodSm:this.selectList.prodSm,
            prodTj:this.selectList.prodTj,
            prodTd:this.selectList.prodTd,
            prodSxzl:this.selectList.prodSxzl,
            prodFl:this.selectList.prodFl,
            maxJe:this.selectList.maxJe,
            jjed:this.selectList.jjed,
            cgfks:this.selectList.cgfks,
            zkfksj:this.selectList.zkfksj,
            dkqs:this.selectList.dkqs,
            dkll:this.selectList.dkll,
            hkfs:this.selectList.hkfs,
            dq:this.selectList.dq,
            byfy:this.selectList.byfy,
            hjfy:this.selectList.hjfy,
            zsfy:this.selectList.zsfy,
            jszq:this.selectList.jszq,
            jstj:this.selectList.jstj,
            jssm:this.selectList.jssm,
            lcPdf:this.selectList.lcPdf,
            bankUrl:this.selectList.bankUrl,
            tghbUrl:this.selectList.tghbUrl,
            xh:this.selectList.xh,
            logoUrl:this.selectList.logoUrl,
            ygmyhk:this.selectList.ygmyhk,
            tkmUrl:this.selectList.tkmUrl,
            lcsm:this.selectList.prodLcsm,
            kfUrl:this.selectList.kfUrl,
            kfBz:this.selectList.kfBz
        }
        //预览图
        if(this.selectList.logoUrl){
          this.bankProd.imgSrc = this.selectList.logoUrl;
          this.isShowImg=true;
        }
        this.lrrq=this.selectList.lrsj;
        this.lrry=this.selectList.lrryMc;
        this.xgry=this.selectList.xgryMc;
        this.xgrq=this.selectList.xgsj;
        this.isShowDialog=true;
      },
      //作废
      cancellation(){
        if (this.selectList == '' || this.selectList == null) {
          this.$message({
            type: 'info',
            message: '请点击一条数据操作!'
          });
          return false;
        }
        if (this.selectList.prodZt == 'N') {
          this.$message({
            type: 'info',
            message: '已是无效数据!'
          });
          return false;
        }
        this.$confirm('是否确定要作废这条记录?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(() => {
            this.operation(this.selectList.prodUuid, 'N');
          })
          .catch(() => {});
      },
      //恢复
      recover(){ 
        if (this.selectList == '' || this.selectList == null) {
          this.$message({
            type: 'info',
            message: '请点击一条数据操作!'
          });
          return false;
        }
        if (this.selectList.prodZt == 'Y') {
          this.$message({
            type: 'info',
            message: '已是有效数据!'
          });
          return false;
        }
        this.$confirm('是否确定要恢复这条记录?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
