        var that = this;
       var json={
            "prodUuid": _prodUuid,
            "prodZt":_yxbz
          }
          
          $.ajax({
            type: 'POST',
            async: true,
            url:this.projectName+"prod/updateProd",
            data:JSON.stringify(json),
            headers:{
                'Authorization':sessionStorage.getItem("tokenHead")+sessionStorage.getItem("token")
            },
            contentType:"application/json",
            dataType: "json",
            timeout: 60000,
            success: function(data, textStatus, jqXHR) {
             if (data.code == 200) {
              that.$message({
                type: 'success',
                message: data.message
              });
              that.querTableData(that.search_cpmc, 1);
              that.$refs.singleTable.setCurrentRow();
            } else {
              that.$message({
                type: 'warning',
                message: data.message
              });
            }
          },
          error: function(xhr, textStatus) {
            console.log(xhr);
          }
          });
